<template>
    <div class="category-data" id="category-data">
        <div class="com-title-bar">
            <div class="box van-hairline--bottom">
                <div class="back" @click="$routerBack"></div>
                <div class="title">{{ $route.query.title }}</div>
                <div class="action">
                </div>
            </div>
        </div>
        <div class="loading" v-if="!view.display">
            <van-loading color="#FFEA03"/>
        </div>
        <van-tabs v-model="tabs.active" lazy-render swipeable animated sticky color="#FFEA03" :line-height="2" :border="false" v-if="view.display">
            <van-tab v-for="(item,index) in category.data" :title="item.title" :name="'category'+item.id" :key="index">
                <div class="data" :style="view.style">
                    <van-list v-model="video.loading[index]" :finished="video.finished[index]" finished-text="没有更多了" @load="moreVideo(index,item.id)">
                        <div class="box">
                            <div class="item" v-for="(value,key) in video.data[index]" :key="key" @click="videoHandel(value.uuid)">
                                <div class="thumbnail">
                                    <img :src="value.thumbnail" alt=""/>
                                </div>
                                <div class="title">
                                    {{ value.translate_title }}
                                </div>
                            </div>
                        </div>
                    </van-list>
                </div>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
    import {Tab, Tabs, Loading, List} from 'vant';

    export default {
        components: {
            [Tab.name]: Tab,
            [Tabs.name]: Tabs,
            [Loading.name]: Loading,
            [List.name]: List
        },
        data() {
            return {
                tabs: {
                    active: 'category0'
                },
                view: {
                    display: false,
                    style: {
                        minHeight: '500px'
                    }
                },
                category: {
                    data: []
                },
                video: {
                    data: [],
                    page: [],
                    loading: [],
                    finished: []
                },
                touch: {
                    startX: 0,
                    startY: 0
                }
            }
        },
        beforeRouteEnter(to, from, next) {
            to.meta.Load = false;
            if (from.path !== '/video/index' || to.meta.Create !== true) {
                to.meta.Load = true;
            }
            next();
        },
        beforeRouteLeave(to, from, next) {
            //标识页面已经加载过
            from.meta.Create = true;
            if (to.path !== '/video/index') {
                this.view.display = false;
            }
            next();
        },
        activated() {
            if (this.$route.meta.Load) {
                this.tabs.active = 'category' + this.$route.query.category;
                this.initCategory();
            }
        },
        created() {
            this.view.style.minHeight = window.innerHeight - 110 + 'px';
        },
        mounted() {
            this.initTouch();
        },
        methods: {
            initTouch() {
                document.getElementById('category-data').addEventListener('touchstart', (e) => {
                    this.touch.startX = e.touches[0].pageX;
                    this.touch.startY = e.touches[0].pageY;
                });
                document.getElementById('category-data').addEventListener('touchmove', (e) => {
                    let moveEndX = e.changedTouches[0].pageX;
                    let moveEndY = e.changedTouches[0].pageY;
                    let X = moveEndX - this.touch.startX;
                    let Y = moveEndY - this.touch.startY;
                    if (Math.abs(Y) > Math.abs(X) && Y < 0) {
                        return;
                    }
                    if (Math.abs(Y) > Math.abs(X) && Y > 0) {
                        return;
                    }
                    e.preventDefault();
                });
            },
            initCategory() {
                let area = this.$route.query.area;
                this.$axios.get('/api/app/category/store', {
                    area: area
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.category.data = result;
                            result.forEach((item, index) => {
                                this.video.data[index] = [];
                                this.video.page[index] = 1;
                                this.video.loading[index] = false;
                                this.video.finished[index] = false;
                                this.getVideo(index, area, item.id, 1);
                            });
                        }
                    });
            },
            getVideo(index, area, category, page) {
                this.$axios.get('/api/app/category/data', {
                    area: area,
                    category: category,
                    page: page
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.video.loading[index] = false;
                            this.video.page[index]++;
                            if (result.data.length === 0) {
                                this.video.finished[index] = true;
                            }
                            let data = this.video.data[index].concat(result.data);
                            this.$set(this.video.data, index, data);
                            this.view.display = true;
                        }
                    })
            },
            moreVideo(index, category) {
                let area = this.$route.query.area;
                let page = this.video.page[index];
                this.getVideo(index, area, category, page);
            },
            videoHandel(uuid) {
                this.$router.push('/video/index?uuid=' + uuid)
            }
        }
    }
</script>

<style scoped>
    .category-data {
        padding: 90px 0 0 0;
    }

    .loading {
        width: 60px;
        margin: 0 auto;
        padding: 100px;
    }

    .category-data .data {
    }

    .category-data .data .box {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 30px 24px 0 24px;
        align-content: flex-start
    }

    .category-data .data .item {
        width: 340px;
        margin: 0 0 30px 0;
    }

    .category-data .data .item .thumbnail {

    }

    .category-data .data .item .thumbnail img {
        width: 340px;
        height: 198px;
        vertical-align: top;
        border-radius: 4px;
        object-fit: cover;
    }

    .category-data .data .item .title {
        font-size: 26px;
        color: #333;
        padding: 10px 0 0 0;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        line-height: 1.4;
        vertical-align: text-top;
    }
</style>